body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
    display: flex;
    font-family: Courier;
    color: #005CB9;
    font-size: medium;
}

.item {
    flex: 1 1;
    padding: 0%;
    border-style: groove;
    border-color: rgb(36, 35, 34);
    /* border-color: rgb(187, 102, 17); */
    position: relative;
    overflow: hidden;
    font-size: 16px
}

.overlay {
    transition: bottom ease 1s;
    position: absolute;
    bottom: -100px;
    left: 0;
    width: 100%;
    padding: -2%;
    opacity: .7;
    background-color: antiquewhite;
}

.item:hover .overlay {
    opacity: 0.6;
    bottom: 0;
}

@media only screen and (max-width: 500px) {
    .container {
        flex-direction: column;
    }
}

.textbox-title{
    flex: 2 1;
    font-family: Courier;
    color: rgb(2, 103, 204);
    font-size: xx-large;
    font-style: oblique <angle>;
}

.textbox-main {
    flex: 2 1;
    font-size: large;
    font-family: Courier;
    color: rgb(73, 153, 233);
    font-style: oblique <angle>;
    padding: -10%;
}

.textbox-sub{
    flex: 2 1;
    font-size: medium;
    font-family: Courier;
    color: #005CB9;
    font-style: oblique <angle>;
    padding: 2%;
}

.aside-1 {
    flex: 1 1;
}

.aside-2 {
    flex: 1 1;
}
.navigation-link {
  display: grid;
  grid-template-columns: reapeat(auto-fit, minmax(350px,1fr));
  justify-content: right;
  margin: 10px;
  padding: 3%;
  height: 70px;
  font-family: Arial;
  color: rgb(120, 176, 241);
}
.navigation-link a:hover {
  background-color: #555;
  color: rgb(120, 176, 241);
}

.navigation-link a {
  display: block;
  color: rgb(88, 85, 85);
  padding: 8px 16px;
  text-decoration: none;
}

.App {
  text-align: center;
  background-color: rgb(36, 35, 34);
}

body{
  background-color: rgb(36, 35, 34)
}

img{
  width: 100%;
  
} 


